/* Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font: inherit;
}

ul[role="list"],
ol[role="list"] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

body {
  text-rendering: optimizeSpeed;
  line-height: 1.3;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* Variables */

:root {
  --clr-primary-dark: #13262f;
  --clr-primary-lt: #faf9f6;
  --clr-accent: #71c839;
  --clr-accent-lt: rgba(112, 200, 57, 0.274);
  --ff-primary: "Montserrat", sans-serif;
  --ff-accent: "Alex Brush", cursive;

  --fw-thin: 300;
  --fw-regular: 400;
  --fw-semi-bold: 600;
  --fw-bold: 700;

  --fs-primary-heading: 2rem;
  --fs-accent-heading: 4rem;
  --fs-subheading: 1.4rem;
  --fs-body: 1.125rem;

  --lh-primary-heading: 1.5em;
  --lh-accent-heading: 1em;

  --size-1: 1rem;
  --size-2: 1.5rem;
  --size-3: 2rem;
  --size-4: 3rem;
  --size-5: 3.5rem;
}

@media (min-width: 992px) {
  :root {
    --fs-primary-heading: 2.5rem;
    --fs-accent-heading: 4.5rem;
    --fs-body: 1.125rem;

    --lh-primary-heading: 1.225em;
    --lh-accent-heading: 1em;
  }
}

